import type { ValueToTextRule } from '@ali/4ever-cangjie';
import { Container } from '../models';

const valueToText: ValueToTextRule = {
  name: 'container',

  match(node) {
    return Container.isContainer(node);
  },

  convert(node, convertor) {
    // 兜底返回空字符串
    return '';
  },
};

export default valueToText;
